//Source Doc Table Get Rows
export function sdGetRows(arr) {
    let rows = [];
    const entries = Object.entries(arr);
    entries.forEach((entry) => {
        if (!entry[0].toLowerCase().includes("id")) {
                rows.push({
                name: entry[0],
                value: entry[1]
            })
        }
        
    })
    return rows;
}
//Responsys Table Properties
 export function editName(propName) {
    switch (propName) {
        case "default_value":
            return 'found value'
        case "cleaned_value":
            return "cleaned value"
        case "source_doc_value":
            return "source doc"
        default:
            return propName
    }
}
//Responys Table Get Rows
export function rrGetRows(field, includedFields) {
    let rows = [];
    const entries = Object.entries(field);
    entries.forEach((entry) => {
        if (includedFields.includes(entry[0])) {
            if (entry[1]){
rows.push({
                name: editName(entry[0]),
                value: entry[1]
            })
            }
                
        }
        
    })
    return rows;
}


//Format value
export function formatValue(val){
    let formattedVal;
    //if value is an array
    if (Array.isArray(val)){
        //if array length is greater than one
        if (val.length > 1){
            formattedVal = <ul>{val.map((v, i)=>{
                return (
                    <li key={`${v}_${i}`}>{formatValue(v)}</li>
                )
            })}</ul>
        } else {
            formattedVal = val.map((v,i)=>{
                return (
                    <span key={`${v}_${i}`}>{formatValue(v)}</span>
                )
            })
        }
    } else if ( //if value is an object
        typeof val === 'object' &&
        !Array.isArray(val) &&
        val !== null
    ) {
       let rows = Object.entries(val);
       formattedVal =  rows.map((row,index)=>{
                return (
                    <div key={`${row}_${index}`}>
                        <span>{row[0]} : </span>
                        <span>{formatValue(row[1])}</span>
                    </div>
                )
            })

    } else if (typeof val === 'string' && val.includes('http')) {
        formattedVal = <a href={val} target="_blank" rel="noreferrer">{val}</a>
    } else {
        formattedVal = <span>{val.toString()}</span>
    }
    return formattedVal;
}

