import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useDataStore } from "../Store/dataStore";
import Box from "@mui/material/Box";
import FilledInput from "@mui/material/FilledInput";
import InputLabel from "@mui/material/InputLabel";
import FormHelperText from "@mui/material/FormHelperText";
import FormControl from "@mui/material/FormControl";
import Button from "@mui/material/Button";
import Cookies from "js-cookie";
import styles from "./Search.module.scss";

export default function HomeSearch() {
  const [values, setValues] = useState({
    spreadsheetId: "",
    endRange: "",
  });
  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const addSourceData = useDataStore((state) => state.addSourceData);
  const clearData = useDataStore((state) => state.clearData);
  let navigate = useNavigate();

  function getData() {
    sessionStorage.clear();
    clearData();
    const spreadsheetId = values.spreadsheetId;
    const endRange = values.endRange;
    let url = `https://docs.google.com/spreadsheets/d/${spreadsheetId}&end=${endRange}`;
    fetch(url, {
      headers: {
        "Access-Control-Allow-Origin": "*",
        method: "cors",
        "Set-Cookie": `session_id=${Cookies.get("session_cookie")}`,
      },
    })
      .then((response) => {
        return response.json();
      })
      .then((response) => {
        addSourceData(response);
        return navigate("/dashboard");
      })
      .catch((err) => {
        console.log(err);
      });
  }
  return (
    <Box className={styles.main}>
      <h2 textAlign="center">Get Source Doc</h2>
      <form className={styles.form}>
        <FormControl fullWidth sx={{ m: 1 }} variant="filled">
          <InputLabel htmlFor="spreadsheet-id">Spreadsheet Id</InputLabel>
          <FilledInput
            id="spreadsheet-id"
            value={values.spreadsheetId}
            onChange={handleChange("spreadsheetId")}
            onBlur={() => {}}
          />
          <FormHelperText id="spreadsheet-id-helper-text">
            <span>
              https://docs.google.com/spreadsheets/d/<b>spreadsheetId</b>
              /edit#gid=0
            </span>
          </FormHelperText>
        </FormControl>
        <FormControl fullWidth sx={{ m: 1 }} variant="filled">
          <InputLabel htmlFor="end-range">Spreadsheet End Range</InputLabel>
          <FilledInput
            id="end-range"
            value={values.endRange}
            onChange={handleChange("endRange")}
          />
          <FormHelperText id="end-range-helper-text">
            <span>
              A1:<b>M10</b>
            </span>
          </FormHelperText>
        </FormControl>
        <Button
          variant="contained"
          disabled={values.spreadsheetId === "" || values.endRange === ""}
          onClick={() => getData()}
        >
          Search
        </Button>
      </form>
    </Box>
  );
}
