import { useState } from "react";
import { Routes, Route, Navigate, redirect, Outlet } from "react-router-dom";
import HomeSearch from "./components/HomeSearch/HomeSearch";
import Dashboard from "./components/Dashboard/Dashboard";
import Report from "./components/Dashboard/Report";
import Login from "./components/Login/Login";
import NoMatch from "./components/NoMatch/NoMatch";
import Sidebar from "./components/Sidebar/Sidebar";
import Box from "@mui/material/Box";
import Cookies from "js-cookie";
import styles from "./App.module.scss";

function checkCookie(name) {
  // if (Cookies.get(name)) {
  //   return true
  // }
  if (sessionStorage.getItem(name) === "true") {
    return true;
  }
  return false;
}

const ProtectedRoute = ({ loggedIn }) => {
  if (loggedIn && checkCookie("loggedIn")) {
    return <Outlet />;
  } else {
    return <Navigate to="/login" replace />;
  }
};

function App() {
  const [loggedIn, setLoggedIn] = useState(checkCookie("loggedIn"));

  return (
    <div className={styles.main}>
      {/* <Nav loggedIn={loggedIn} setLoggedIn={setLoggedIn} /> */}
      <Box className={styles.mainContainer}>
        {loggedIn && <Sidebar setLoggedIn={setLoggedIn} />}
        <Routes>
          <Route
            path="/login"
            element={<Login loggedIn={loggedIn} setLoggedIn={setLoggedIn} />}
          />
          <Route element={<ProtectedRoute loggedIn={loggedIn} />}>
            <Route path="/" element={<HomeSearch />} />
            <Route path="/dashboard" element={<Dashboard />} />
            <Route path="/dashboard/:id" element={<Report />} />
          </Route>
          <Route path="*" element={<NoMatch />} />
        </Routes>
      </Box>
    </div>
  );
}

export default App;
