import { useState } from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import Link from "@mui/material/Link";
import { useDataStore } from "../Store/dataStore";
import styles from "./Sidebar.module.scss";
import MenuIcon from "@mui/icons-material/Menu";
import MenuOpenIcon from "@mui/icons-material/MenuOpen";
import { useLocation } from "react-router-dom";

const pages = [
  {
    title: "Search",
    path: "/",
  },
  {
    title: "Dashboard",
    path: "/dashboard",
  },
];

const isPageActive = (path) => {
  if (path === "/") {
    return path === window.location.pathname;
  } else {
    return window.location.pathname.includes(path);
  }
};

const isEmailActive = (path, id) => {
  let emailId = id.toString();
  return path.split("/")[2] === emailId;
};
export default function Sidebar(props) {
  const { setLoggedIn } = props;
  const emails = useDataStore((state) => state.sourceData);
  const toggleMenu = useDataStore((state) => state.toggleMenu);
  const menuOpen = useDataStore((state) => state.menuOpen);
  const location = useLocation();
  const locationPath = location.pathname;

  const handleToggle = () => {
    toggleMenu();
  };
  const handleLogout = () => {
    sessionStorage.removeItem("loggedIn");
    sessionStorage.clear();
    setLoggedIn(false);
  };

  return (
    <Box className={`${styles.main} ${!menuOpen ? styles.mainClosed : ""}`}>
      <Button onClick={handleToggle} className={styles.button} disableRipple>
        {menuOpen ? <MenuOpenIcon /> : <MenuIcon />}
      </Button>
      <Box className={styles.wrapper}>
        <Grid>
          <nav>
            <List>
              {pages.map((page, i) => {
                return (
                  <ListItem disablePadding key={`${page}_${i}`}>
                    <ListItemButton>
                      <Link
                        href={page.path}
                        className={
                          isPageActive(page.path) ? styles.linkActive : ""
                        }
                      >
                        <ListItemText primary={page.title} />
                      </Link>
                    </ListItemButton>
                  </ListItem>
                );
              })}
            </List>
          </nav>
        </Grid>
        <Grid className={styles.emailGrid}>
          <List>
            {emails.length > 0 &&
              emails.map((email, index) => (
                <ListItem key={`${index}_${email.id}`} disablePadding>
                  <ListItemButton>
                    <Link
                      href={`/dashboard/${email.id}`}
                      className={
                        isEmailActive(locationPath, email.id)
                          ? styles.linkActive
                          : ""
                      }
                    >
                      <ListItemText
                        primary={`${index}  ${email["Template Name"]}`}
                      />
                    </Link>
                  </ListItemButton>
                </ListItem>
              ))}
          </List>
        </Grid>
        <Grid className={styles.logoutGrid}>
          <ListItem disablePadding>
            <ListItemButton
              onClick={() => {
                handleLogout();
              }}
            >
              <ListItemText primary="Logout" />
            </ListItemButton>
          </ListItem>
        </Grid>
      </Box>
    </Box>
  );
}
