import Box from "@mui/material/Box";
import SourceDoc from "./SourceDoc";
import Responsys from "./Responsys";
import { useDataStore } from "../Store/dataStore";
import { useParams } from "react-router-dom";
import styles from "./Dashboard.module.scss";

export default function Report() {
  const params = useParams();
  const sourceData = useDataStore((state) => state.sourceData);
  const campaignData = sourceData.filter((email) => {
    return +email.id === +params.id;
  })[0];
  const menuOpen = useDataStore((state) => state.menuOpen);
  return (
    <Box className={styles.main}>
      <Box className={styles.wrapper}>
        <Box component="main" className={styles.report}>
          <Box className={styles.title}>
            <h1>{campaignData["Template Name"]}</h1>
          </Box>
          <Box className={`${styles.body} ${menuOpen && styles.bodyOpen}`}>
            <SourceDoc campaignData={campaignData} params={params} />
            <Responsys campaignData={campaignData} params={params} />
          </Box>
        </Box>
      </Box>
    </Box>
  );
}
