import * as React from 'react';
import Typography from '@mui/material/Typography';
import { rrGetRows, formatValue} from "../../utils/util";
import styles from "./Tables.module.scss";

export default function FieldTable(props) {

    const {field, includedFields, titleKey, title = null,keyString} = props;
    const rows = rrGetRows(field,includedFields);

    return (
        <div className={styles.fieldTableWrapper}>
            <div className={styles.fieldTableTitle}>
                    <Typography variant="p">
                        {field[titleKey] || title}
                    </Typography>
            </div>
            <table className={`${styles.table} ${styles.fieldTable}`}>
                <tbody>
                    {rows.map((row,index)=>{
                        return (
                            <tr key={`${field[titleKey]}_${index}`}>
                                <td className={styles.headerCol}>{row.name}</td>
                                <td className={styles.valueCol}>{formatValue(row.value)}</td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </div>
    );
}
