import * as React from 'react';
import { formatValue } from "../../utils/util";
import styles from "./Tables.module.scss";

export default function Table(props) {

    const {title, rows} = props;

    return (
        <table className={styles.table}>
            {title &&
                <thead>
                    <tr>{title}</tr>
                </thead>
            }
            <tbody>
                {rows.map((row,index)=>{
                    return (
                        <tr  key={`${index}_row_value_`}>
                            <td className={styles.headerCol}>{row.name}</td>
                            <td className={styles.valueCol}>{formatValue(row.value)}</td>
                        </tr>
                    )
                })}
            </tbody>
        </table>
    );
}
