import create from 'zustand';
import { persist } from 'zustand/middleware'
import DATA from "../../exampleData/responsys.json"
import SPREADSHEET from "../../exampleData/spreadsheet.json"
const useStore = create(
    persist(
        set => ({
            menuOpen: false,
            checklistOpen: false,
            sourceData: [],
            responsysData: [],
            checklist:[],
            toggleMenu: () => {
                set(state=>({
                    menuOpen: !state.menuOpen
                }))
            },
            toggleChecklist: () => {
                set(state=>({
                    checklistOpen: !state.checklistOpen
                }))
            },
            addSourceData: (data) =>
                set(state => ({
                    sourceData: [...data]
                })),
            addResponsysData: (data) => {
                set(state => ({
                    responsysData: [{ ...data }, ...state.responsysData]
                }))
            },
            clearData: () => set(state => ({
                sourceData: [],
                responsysData:[]
            }))
        }),
        {
            name: 'email-storage', 
            getStorage: () => sessionStorage, // (optional) by default the 'localStorage' is used
        }
    )
);

export const useDataStore = useStore;
