import { useDataStore } from "../Store/dataStore";
import { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Accordion from "../Accordion/Accordion";
import FieldTable from "../Tables/FieldTable";
import styles from "./Dashboard.module.scss";

function ResponsysReport({ data }) {
  const {
    about,
    dataSource,
    targeting,
    message,
    links,
    id,
    name,
    error,
    status,
  } = data;

  if (error) {
    return (
      <Box className={styles.reportWrapper}>
        <Typography textAlign="left">Error Status {status}</Typography>
        <Typography textAlign="left" sx={{ wordBreak: "break-word" }}>
          {error}
        </Typography>
      </Box>
    );
  }
  return (
    <Box className={styles.reportWrapper}>
      <Accordion title="About">
        {about.fields.map((field, index) => {
          return (
            <FieldTable
              key={`About_${index}`}
              field={field}
              includedFields={["default_value", "source_doc_value", "matched"]}
              titleKey="name"
              keyString="About"
            />
          );
        })}
      </Accordion>

      <Accordion title="Data Source">
        {dataSource.fields.map((field, index) => {
          return (
            <FieldTable
              key={`DataSource_${index}`}
              field={field}
              includedFields={[
                "type",
                "default_value",
                "source_doc_value",
                "cleaned_value",
                "matched",
              ]}
              titleKey="name"
              keyString="DataSource"
            />
          );
        })}
      </Accordion>

      <Accordion title="Targeting">
        {targeting.fields.map((field, index) => {
          return (
            <FieldTable
              key={`Targeting_${index}`}
              field={field}
              includedFields={["default_value", "source_doc_value", "matched"]}
              titleKey="name"
              keyString="Targeting"
            />
          );
        })}
      </Accordion>

      <Accordion title="Messaging">
        {message.fields.map((field, index) => {
          return (
            <FieldTable
              key={`Messaging_${index}`}
              field={field}
              includedFields={[
                "default_value",
                "source_doc_value",
                "cleaned_value",
                "matched",
              ]}
              titleKey="name"
              keyString="Messaging"
            />
          );
        })}
      </Accordion>

      <Accordion title="Launched Links">
        {links.fields.map((field, index) => {
          return (
            <FieldTable
              key={`LaunchedLinks_${index}`}
              field={field}
              includedFields={["class", "href", "html", "launchedPage"]}
              title={`Launched Links ${index + 1}`}
              keyString="LaucnhedLinks"
            />
          );
        })}
      </Accordion>
    </Box>
  );
}
function GenerateReport({ campaignName, setReport, setShowReport, params }) {
  const addResponsysData = useDataStore((state) => state.addResponsysData);
  const [loading, setLoading] = useState(false);
  function getResponsys() {
    setLoading(true);
    let url = `${process.env.REACT_APP_ENDPOINT}/campaign?campaignName=${campaignName}`;
    fetch(url)
      .then((response) => {
        return response.json();
      })
      .then((response) => {
        setLoading(false);
        if (response.error) {
          let res = {
            id: params.id,
            ...response,
          };
          addResponsysData(res);
          setReport(res);
        } else {
          addResponsysData(response);
          setReport(response);
        }
        console.log("response", response);
        setShowReport(true);
      })
      .catch((err) => {
        setLoading(false);
        console.log(err);
      });
  }
  return (
    <Box>
      <Button
        variant="contained"
        onClick={() => getResponsys()}
        disabled={loading ? true : false}
      >
        {loading ? "Loading" : "Get Responsys"}
      </Button>
    </Box>
  );
}
export default function Responsys({ campaignData, params }) {
  const [report, setReport] = useState({});
  const [showReport, setShowReport] = useState(false);
  const responsysData = useDataStore((state) => state.responsysData);

  useEffect(() => {
    if (responsysData.length !== 0) {
      let arr = responsysData.filter((obj) => {
        return +obj.id === +params.id;
      });
      if (arr.length > 0) {
        setReport(arr[0]);
        setShowReport(true);
      }
    }
    //eslint-disable-next-line
  }, []);
  return (
    <Box className={styles.responsys}>
      <Typography variant="h6" sx={{ padding: "10px 0" }}>
        Responsys
      </Typography>
      {!showReport && (
        <GenerateReport
          campaignName={campaignData["Template Name"]}
          setReport={setReport}
          setShowReport={setShowReport}
          params={params}
        />
      )}
      {showReport && <ResponsysReport data={report} />}
    </Box>
  );
}
